* {
    margin: 0px;
    padding: 0px;
}

.fullhome {
    background-color: rgb(1, 3, 1);
}
.btn-floating{
    z-index: 0;
}
.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: url('me.png');
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    width: 90%;
    margin: 0 auto;
}

.scroll {
    margin-top: 100px;
}

.pars {
    margin-left: 10px;
    color: red;
 
}

.material-icons {
    display: flex;
    justify-content: center;
    align-items: center;
}

.intro {
    margin-top: 300px;
    color: #fff;
    font-size: 4em;
    font-weight: 900;
    z-index: 0;
    overflow: hidden;
    -webkit-box-reflect: below -12px linear-gradient(transparent,rgba(0,0,0,0.2));
}

.intro span {
    color: #ff022c;
}